<template>
    <div class="register-admin">
        <iframe :src="registerAdminUrl" frameborder="0"></iframe>
    </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'register',
    data () {
      return {
        registerAdminUrl: ''
      }
    },
    mounted () {
      axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
      axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
      axios.get('/appConfig/getMonitorHosts', {params: {name: 'redefine.monitor.register'}}).then(res => {
        this.registerAdminUrl = res.data
      })
    }
    // computed: {
    //   registerAdminUrl () {
    //     var map = {
    //       'dev-watchdog.welike.in': 'http://dev-eureka.welike.in',
    //       'pre-watchdog.welike.in': 'http://pre-eureka.welike.in',
    //       'watchdog.welike.in': 'http://eureka.welike.in'
    //     }
    //     if (window.env === 'development' || location.host.includes('localhost')) {
    //       return 'http://dev-eureka.welike.in'
    //     }
    //     return map[location.host]
    //   }
    // }
  }
</script>

<style>
    .register-admin {
        left: 0px;
        right: 0px;
        margin-top: -20px;
        width: 100%;
        height: 100%;
        position: absolute;
    }

    .register-admin iframe {
        height: 100%;
        width: 100%;
        overflow: auto;
    }

    .el-main {
        position: relative;
    }
</style>


